<template>
	<scroll-view class="article_scroll_view" scroll-y @scrolltolower="handleToLower"
		style="height: 100vh;width: 100%;">

		<view class="uni-list" v-if="listData.length>0">
			<view class="item" v-for="(item, index) in listData" :key="index" >
				<image scaleToFill class="uni-media-list-logo2" :src="item.articleCover"  @click="goDetail(item)"></image>
				<view class="uni-media-list-title" @click="goDetail(item)">{{ item.cmsName }}</view>
				<view class="uni-media-list-content">{{ item.cmsContent }}</view>
				<view class="uni-media-list-btn" @click.stop="detailHandle(item.cmsName)">详情点击</view>
			</view>
		</view>

		<view v-if="listData.length==0" style="margin: 0 20px;text-align: center;">暂无文章列表</view>
		<!-- <uni-load-more :status="status" :icon-size="8" :content-text="contentText" /> -->
	</scroll-view>
</template>

<script>
	export default {
		props: {

		},
		data() {
			return {
				hasMore: false,
				banner: {},
				listData: [],
				menuId: '',
				reload: false,
				status: 'more',
				contentText: {
					contentdown: '上拉加载更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				}
			};
		},
		onLoad(obj) {
			this.menuId = obj.id
			//console.log('wwwwww',this.menuId)
			this.getList()
		},
		created() {

		},
		mounted() {
			//this.getList()
		},
		onPullDownRefresh() {
			this.reload = true;
			this.getList();
		},
		onReachBottom() {
			this.status = 'more';
			this.getList();
		},
		methods: {
			getList() {
				this.request({
					url: "/menu/cms/list",
					data: {
						menuId: this.menuId
					}
				}).then(data => {
					if (data && data.resphead && data.resphead.succeed) {
						this.listData = data.respbody

					} else {
						this.isShow = false
						uni.showToast({
							title: data.resphead.message || "获取文章列表失败",
							icon: "none"
						});
					}
				});
			},
			goDetail(item) {
				uni.navigateTo({
					url: '/pages/article/article-detail?id=' + item.cmsId
				});
			},
			detailHandle(name){
				//console.log(window.location.hostname)
				//#ifdef APP-PLUS
				plus.runtime.openURL(`/demox/sample-apps/`)
				// #endif
				//#ifdef H5
				location.href=`/demox/sample-apps/`
				// #endif
			},
			handleToLower() {
				// if (this.hasMore) {
				//   this.params.skip += this.params.limit;
				//   this.getList();
				// } else {
				//   uni.showToast({
				//     title: "没有数据了",
				//     icon: "none"
				//   });
				// }
			}
		}
	};
</script>

<style lang="scss" scoped>
	.banner {
		height: 300rpx;
		// overflow: hidden;
		position: relative;
		background-color: #ccc;
	}

	.banner-img {
		width: 100%;
		background-color: #cccccc;
	}

	.banner-title {
		overflow: hidden;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		font-size: 32rpx;
		font-weight: 400;
		line-height: 42rpx;
		white-space: pre-wrap;
		color: white;
		z-index: 11;
		padding: 30rpx;
		background: rgba($color: #000000, $alpha: 0.5);
	}

	.uni-list {
		background-color: #FFFFFF;
		position: relative;
		width: 100%;
		display: flex;
		flex-direction: column;
	}

	.item {
		position: relative;
		padding:22px 30rpx 40rpx;
		height: auto;
		overflow: hidden;
	}



	.uni-media-list-logo2 {
		width: 100%;
		display: inline-block;
		margin-bottom: 48rpx;
	}

	.uni-media-list-title {
		font-size: 54rpx;
		line-height: 64rpx;
		overflow: hidden;
		width: 100%;
		margin-bottom: 32rpx;
		font-weight: 700;
		color: #000704;
	}

	.uni-media-list-content {
		width: 100%;
		line-height: 56rpx;
		font-size: 38rpx;
		color: #696969;
		margin-bottom: 48rpx;
	}
	.uni-media-list-btn{
		margin-bottom: 48rpx;
		font-size: 34rpx;
		border-bottom: 4rpx solid #189a46;
		color: #000704;
		font-weight: 700;
		display: inline-block;
		cursor: pointer;
	}
</style>
